@font-face {
  font-family: "Akkurat";
  src: url("typography/Akkurat-Normal.eot");
  src:
  url("typography/Akkurat-Normal.ttf") format('truetype'),
  url("typography/Akkurat-Normal.woff") format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Akkurat";
  src: url("typography/Akkurat-Bold.eot");
  src:
  url("typography/Akkurat-Bold.ttf") format('truetype'),
  url("typography/Akkurat-Bold.woff") format('woff');
  font-weight: bold;
  font-style: bold;
}

@font-face {
  font-family: "Akkurat";
  src: url("typography/Akkurat-Normal-Italic.eot");
  src:
  url("typography/Akkurat-Normal-Italic.ttf") format('truetype'),
  url("typography/Akkurat-Normal-Italic.woff") format('woff');
  font-weight: normal;
  font-style: italic;
}

@font-face {
  font-family: "Akkurat";
  src: url("typography/Akkurat-Bold-Italic.eot");
  src:
  url("typography/Akkurat-Bold-Italic.ttf") format('truetype'),
  url("typography/Akkurat-Bold-Italic.woff") format('woff');
  font-weight: bold;
  font-style: italic;
}

body {
  overflow-x: hidden;
}

.App {
  text-align: center;
  color: #222629;
}

#hero {
  background: url('./img/chart-background@2x.png') no-repeat;
  background-position: top center;
  background-size: 2829.16px;
}

.description {
  font-family: 'Akkurat', Helvetica, Arial, sans-serif;
  font-size: 21px;
}

.how-it-works h3 {
  font-family: 'Akkurat', Helvetica, Arial, sans-serif;
}

.georgia {
  font-family: Georgia, 'Times New Roman', Times, serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.hero-image {
  margin-top: 60px;
}

#hero {
  color: #222629;
  text-align: center;
}

img {
  max-width: 100%;
  overflow: visible;
  height: auto;
}

.nav-logo {
  padding: 1.5rem 1rem;
}

.highlighter {
  display: inline-block;
  background-color: #7DD8EF80;
}


.navbar {
  width: 100%;
}

.navbar-toggler {
  float: right;
}

.col {
  vertical-align: middle;
}

#hero h1 {
  width: 80%;
}

#examples h2 {
  margin-bottom: 43px;
}

.interlude {
  text-align: center;
  border-top: 1px solid #50B9D3;
  border-bottom: 1px solid #50B9D3;
  color: #222629;
  margin-top: 58px;
  margin-bottom: 58px;
  padding-top: 30px;
  padding-bottom: 30px;
  font-size: 28px;
}

.how-it-works {
  background-color: #E8F7FA;
  margin-top: 122px;
}

.how-it-title {
  margin-top: 50px;
}

.bullet {
  margin-right: 8px;
  margin-bottom: 18px;
}

.case-study {
  background: linear-gradient(#FFF, #EDF2F5);
  border-top: 1px solid #D2DFE8;
}

p.how-it {
  width: 95%;
  font-family: Georgia, 'Times New Roman', Times, serif;
}

.overflowing {
  margin-left: 50%;
  transform: translateX(-50%);
  box-shadow: 0px 3px 40px #57677240;
  border-radius: 40px;

}

.comparisons {
  margin-top: 94px;
  margin-bottom: 49px;
}

.clients {
  margin-top: -57px;
}

.roi {
  margin-top: 57px;
  margin-bottom: 85px;
}

.roi h3 {
  line-height: 3rem;
  width: 90%;
}

.comparison-title {
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 24px;
  font-weight: bold;
  font-style: italic;
  color: #36A7C4;
  margin-bottom: 15px;
}

.circled-avinew {
  display: inline;
  width: 83px;
  height: 83px;
}

.circled-n2uitive {
  display: inline;
  width: 84px;
  height: 85px;
}

.circled-fireside {
  display: inline;
  width: 76px;
  height: 76px;
}

.circled-avinew::before {
position: absolute;
content: '';
background: url('./img/circled-graphic@2x.png');
background-size: 115%;
background-position: center;
width: 83px;
height: 83px;
pointer-events: none;
margin-top: -15px;
margin-left: -8px;
}

.circled-n2uitive::before {
  position: absolute;
  content: '';
  background: url('./img/circled-graphic@2x.png');
  background-size: 118%;
  background-position: center;
  width: 84px;
  height: 85px;
  pointer-events: none;
  margin-top: -15px;
  margin-left: -7px;
  }

  .circled-fireside::before {
    position: absolute;
    content: '';
    background: url('./img/circled-graphic@2x.png');
    background-size: 115%;
    background-position: center;
    width: 76px;
    height: 76px;
    pointer-events: none;
    margin-top: -10px;
    margin-left: -7px;
    }

.fireside-roi {
  color: #FF6161;
  text-decoration: underline;
}

.avinew-roi {
  color: #4D48BD;
  text-decoration: underline;
}

.n2uitive-roi {
  color: #0E929F;
  text-decoration: underline;
}

.contact-area {
  background-color: #E8F7FA;
  padding-top: 100px;
  padding-bottom: 900px;
}

.contact-boxes {
  padding-top: 49px;
}

.contact-boxes h3 {
  font-size: 27px;
}

.contact-boxes p{
  font-size: 20px;
  /*width: 80%;*/
}
.contact-boxes label {
  font-size: 15px;
}

.contact-buttons {
  padding-top: 7px;
}

.contact-button {
  text-align: center;
  font-size: 16px;
  font-family: 'Akkurat', Hevletica, Arial, sans-serif;
  font-weight: bold;
  color: #FFF;
  background-color: #50B9D3;
  width: 80px;
  padding-top: 4px;
  padding-bottom: 4px;
  border: 1px solid #36A7C4;
  border-radius: 88px;
  box-shadow: 0px 0px 7px #00CBFF54;
  margin-top: 30px;
}

form.contact_form label.checkbox {
  display: block;
  margin-left: 18px;
  font-size: 20px;
  float: left;
}

form.contact_form label.textarea {
  font-size: 20px;
}

form.contact_form label input[type='checkbox']{margin-left:-18px; border-color: #A7B6C1;}

.jagged {
  padding-top: 63px;
  padding-bottom: 63px;
}



@media (min-width: 1200px) {
.container, .container-lg, .container-md, .container-sm, .container-xl {
    max-width: 1080px !important;
}
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.header {
  width: 100%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
